import React, { useState, useEffect } from 'react';

import Header from './Header.js';
import Main from './Main.js';
import Footer from './Footer.js';
import api from '../utils/api.js';
import ImagePopup from './ImagePopup.js'
import EditProfilePopup from './EditProfilePopup.js'
import EditAvatarPopup from './EditAvatarPopup.js'
import AddPlacePopup from './AddPlacePopup.js';
import { CurrentUserContext } from '../contexts/CurrentUserContext.js';

function App() {
    const [editAvatarPopupOpen, setIsEditAvatarOpen] = useState(false);
    const [editProfilePopupOpen, setIsEditProfileOpen] = useState(false);
    const [addPlacePopupOpen, setIsAddPlaceOpen] = useState(false);
    const [selectedCard, setSelectedCard] = useState(null);
    const [currentUser, setCurrentUser] = useState({});
    const [cards, setCards] = useState([]);

    //currentUser
    React.useEffect(() => {
        api.getUserInfo()
            .then((res) => {
                setCurrentUser({
                    ...currentUser,
                    name: res.name,
                    about: res.about,
                    avatar: res.avatar,
                    _id: res._id
                })
            })
            .catch((err) => {
                console.log(err);
            })
    }, [])

    // cards
    useEffect(() => {
        api.getCardsList()
            .then((res) => {
                setCards(res)
            })
            .catch((err) => {
                console.log(err);
            });
    }, []);

    function handleCardLike(card) {
        const isLiked = card.likes.some(i => i._id === currentUser._id);

        if (!isLiked) {
            api.addLike(card._id)
                .then((newCard) => {
                    setCards((state) => state.map((c) => c._id === card._id ? newCard : c))
                })
                .catch((err) => {
                    console.log(err);
                })
        }
        else {
            api.deleteLike(card._id)
                .then((newCard) => {
                    setCards((state) => state.map((c) => c._id === card._id ? newCard : c))
                })
                .catch((err) => {
                    console.log(err);
                })
        }
    }

    function handleCardDelete(card) {
        api
            .removeCard(card._id)
            .then(() => {
                const updatedList = cards.filter((c) => c._id !== card._id);
                setCards(updatedList);
            })
            .catch((err) => {
                console.log(err);
            });

    }

    //popups
    function handleEditAvatarOpen() {
        setIsEditAvatarOpen(true);
    }

    function handleEditProfileOpen() {
        setIsEditProfileOpen(true);
    }

    function handleAddPlaceOpen() {
        setIsAddPlaceOpen(true);
    }

    function handleCardClick(card) {
        setSelectedCard(card)
    }

    function closeAllPopups() {
        setIsEditAvatarOpen(false);
        setIsEditProfileOpen(false);
        setIsAddPlaceOpen(false);
        setSelectedCard(null);
    }

    function handleUpdateUser(newUser) {
        api.setUserInfo({ name: newUser.name, about: newUser.about })
            .then((res) => {
                setCurrentUser({
                    ...currentUser,
                    name: newUser.name,
                    about: newUser.about
                })
            })
            .then(closeAllPopups)
            .catch((err) => {
                console.log(err);
            })
    }

    function handleUpdateAvatar(newUser) {
        api.setUserPic({ avatar: newUser.avatar })
            .then(() => {
                setCurrentUser({
                    ...currentUser,
                    avatar: newUser.avatar
                })
            })
            .then(closeAllPopups)
            .catch((err) => {
                console.log(err);
            })
    }

    function handleAddPlaceSubmit(card) {
        api.addCard({ name: card.name, link: card.link })
            .then((card) => {
                setCards([card, ...cards])
            })

            .then(closeAllPopups)
            .catch((err) => {
                console.log(err);
            })
    }

    return (
        <CurrentUserContext.Provider value={currentUser}>
            <div className="App">
                <Header />

                <Main
                    onEditAvatar={handleEditAvatarOpen}
                    onEditProfile={handleEditProfileOpen}
                    onAddPlace={handleAddPlaceOpen}
                    handleCardClick={handleCardClick}
                    onClose={closeAllPopups}
                    cards={cards}
                    onCardLike={handleCardLike}
                    onCardDelete={handleCardDelete}
                >
                </Main>

                <Footer />

                <EditProfilePopup isOpen={editProfilePopupOpen} onClose={closeAllPopups} onUpdateUser={handleUpdateUser} />

                <EditAvatarPopup isOpen={editAvatarPopupOpen} onClose={closeAllPopups} onUpdateAvatar={handleUpdateAvatar} />

                <AddPlacePopup isOpen={addPlacePopupOpen} onClose={closeAllPopups} onAddPlace={handleAddPlaceSubmit} />

                <ImagePopup
                    card={selectedCard}
                    onClose={closeAllPopups}
                />
            </div>
        </CurrentUserContext.Provider>
    )
}

export default App